.model {
  position: fixed;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70% !important;
  padding: 2em;
  background: #f3f3f3;
  z-index: 1200 !important;
  min-height: unset !important;
  height: fit-content !important;
  border-radius: 5px;
  overflow: auto;

  @media (max-width: 576px) {
    width: 90%;
  }

  .image_container {
    > div {
      height: 200px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-radius: 5px;
      }
    }
  }

  .close_icon {
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    font-size: 1em;
    z-index: 1;
  }
}
.content li {
  list-style-type: none;
}

h1 {
  color: crimson;
}
